import Weather from "./modules/search";

window.addEventListener('DOMContentLoaded', () => {
    let d = new Date();
    document.getElementById("day").innerHTML = d.getDate();
    let month = new Array();
    month[0] = "January";
    month[1] = "February";
    month[2] = "March";
    month[3] = "April";
    month[4] = "May";
    month[5] = "June";
    month[6] = "July";
    month[7] = "August";
    month[8] = "September";
    month[9] = "October";
    month[10] = "November";
    month[11] = "December";
    document.getElementById("month").innerHTML = month[d.getMonth()];

    Weather();
})
